export default {
	data() {
		this.chartSettings1 = {
			labelMap: {
				hour: '日期',
			},
			area: true,
		};
		return {
			chartData1: { //columns必须与rows同语言
				columns: ['hour', 'td_new_user_num'],
				rows: [
					{ 'hour': '1场', 'td_new_user_num': 1},
					{ 'hour': '2场', 'td_new_user_num': 2},
					{ 'hour': '3场', 'td_new_user_num': 3},
					{ 'hour': '4场', 'td_new_user_num': 4},
					{ 'hour': '5场', 'td_new_user_num': 5},
				]
